import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'

export default {
	props: {
		tabBars: Array,
		tabIndex: Number
	},
	data () {
		return {
			
		}
	},
	components: {
		uniNavBar
	},
	methods: {
		fixIndex (index) {
			this.$emit('chang-tab',index)
		},
		openAdd () {
			uni.navigateTo({
